img {
  width: 100%;
  object-fit: cover;
  height: 210px;
  display: block;
}

body {
  text-align: center;
  margin: 0;
  padding: 85px 0 65px;
}

.container {
  width: 1366px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

header {
  width: 100%;
  background-color: #8a2be2;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  text-align: center;
  width: 150px;
  height: 50px;
  margin: 20px auto auto;
  display: flex;
}

.btn_submit {
  height: 25px;
  background-color: #eea908f6;
  border-color: #0000;
  border-radius: 10px;
  margin-left: 5px;
}

input {
  width: 150px;
  height: 20px;
  border-color: #0000;
  border-radius: 10px;
}

.info {
  width: 330px;
  display: flex;
}

.info-item {
  width: 100%;
  width: 25%;
  padding: 0 9px;
  font-size: 15px;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 22.5px);
  width: 330px;
}

.load-more {
  color: #fff;
  width: 130px;
  height: 40px;
  background-color: #0b76ea;
  border-color: #0000;
  border-radius: 5px;
}

.is-hidden {
  display: none;
}

spinner {
  width: 10px;
  height: 10px;
  display: flex;
}

spinner:before {
  content: " ";
  width: 10px;
  height: 10px;
  border: 6px solid #fff;
  border-color: #fff #0000;
  border-radius: 50%;
  margin: 8px;
  animation: 1.2s linear infinite lds-dual-ring;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

